.feed {
  align-items: center;
  display: flex;
  line-height: 1;
  overflow: hidden;
  padding: 1.5rem 0;
  position: relative;
}
.feed:hover .feed-title {
  opacity: 0.8;
}
.feed:hover .feed-right {
  margin-right: 1.6rem;
}
.feed:hover .feed-icon {
  transform: translateX(0.4rem);
}
.feed + .feed {
  border-top: 1px solid var(--light-gray-color);
}
.feed + .feed.feed-paged {
  border-top: 0;
  margin-top: 3rem;
}
.feed-calendar {
  color: var(--brand-color);
  display: flex;
  font-size: 1.1rem;
  font-weight: 800;
  margin-right: 1.5rem;
  white-space: nowrap;
}
.feed-calendar-month {
  margin-right: 0.4rem;
  text-transform: uppercase;
}
.feed-title {
  flex-grow: 1;
  font-size: 1.6rem;
  font-weight: 400;
  margin-bottom: 0;
  overflow: hidden;
  padding-right: 1rem;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.feed-right {
  align-items: center;
  display: flex;
  transition: margin 0.2s var(--animation-base);
}
.feed-visibility {
  color: var(--orange-color);
  height: 16px;
  margin-right: 1rem;
  width: 16px;
}
.feed-visibility-public {
  display: none;
}
.feed-length {
  color: var(--secondary-text-color);
  font-size: 1.4rem;
  white-space: nowrap;
}
.feed-icon {
  color: var(--secondary-text-color);
  height: 16px;
  position: absolute;
  right: 0;
  transform: translateX(2rem);
  transition: transform 0.2s var(--animation-base);
  width: 16px;
}
