.site-footer {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 3rem 0 4.5rem;
}
.social {
  display: flex;
  margin-bottom: 2rem;
}
.social-item {
  color: var(--secondary-text-color);
  line-height: 0;
  margin: 0 4px;
}
.social-item .icon {
  height: 24px;
  width: 24px;
}
.copyright {
  color: var(--secondary-text-color);
  font-size: 1.2rem;
  letter-spacing: -0.02rem;
}
.copyright a {
  color: var(--secondary-text-color);
}
.copyright a:hover {
  opacity: 1;
  text-decoration: underline;
}
.footer-nav {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 0.5rem;
}
.footer-nav .menu-item {
  align-items: center;
  color: var(--secondary-text-color);
  display: flex;
  font-size: 1.2rem;
  font-weight: 400;
  margin: 0;
}
.footer-nav .menu-item + .menu-item:before {
  content: "\02022";
  font-family: serif;
  line-height: 1;
  padding: 0 0.7rem;
}
.footer-nav .menu-item:hover {
  opacity: 1;
}
.theme-icon {
  height: 1.1rem;
  width: 1.1rem;
}
.theme-text {
  margin-left: 0.3rem;
}
.theme-icon-dark,
.theme-icon-light {
  display: none;
}
.theme-dark .theme-icon-system,
.theme-dark .theme-icon-light {
  display: none;
}
.theme-dark .theme-icon-dark {
  display: block;
}
.theme-light .theme-icon-system,
.theme-light .theme-icon-dark {
  display: none;
}
.theme-light .theme-icon-light {
  display: block;
}
